<template>
  <div class="form-container">
    <form @submit.prevent="handleSubmit">
      <div class="form-grid">
        <div class="form-group">
          <label>SKU</label>
          <input v-model="comicBook.sku" type="text" placeholder="Enter SKU" />
        </div>
        <div class="form-group">
          <label>Name</label>
          <input v-model="comicBook.name" type="text" placeholder="Enter name" />
        </div>
        <div class="form-group">
          <label>Genre</label>
          <input v-model="comicBook.genre" type="text" placeholder="Enter genre" />
        </div>
        <div class="form-group">
          <label>ISBN</label>
          <input v-model="comicBook.isbn" type="text" placeholder="Enter ISBN" />
        </div>
        <div class="form-group">
          <label>Weight</label>
          <input v-model="comicBook.weight" type="text" placeholder="Enter weight" />
        </div>
        <div class="form-group">
          <label>Price</label>
          <input v-model="comicBook.price" type="text" placeholder="Enter price" />
        </div>
        <div class="form-group">
          <label>Release Date</label>
          <input v-model="comicBook.releaseDate" type="date" placeholder="Enter release date" />
        </div>
        <div class="form-group">
          <label>Description</label>
          <textarea v-model="comicBook.description" placeholder="Enter description"></textarea>
        </div>
      </div>

      <h3>Authors Details</h3>
      <div class="form-grid">
        <div class="form-group">
            <label for="authors">Authors</label>
            <select id="authors" v-model="comicBook.authors" multiple required class="full-width-select">
              <option v-for="author in authorsList" :key="author.authorID" :value="author.authorID">{{ author.authorID }}</option>
            </select>
        </div>
      </div>

      <h3>Publisher Details</h3>
      <div class="form-grid">
        <div class="form-group">
          <label>Name</label>
          <input v-model="comicBook.publisher.name" type="text" placeholder="Enter name" />
        </div>
        <div class="form-group">
          <label>Year Found</label>
          <input v-model="comicBook.publisher.yearFound" type="text" placeholder="Enter year found" />
        </div>
      </div>

      <div class="form-group">
        <label>Upload Photo</label>
        <input type="file" @change="handleFileUpload" />
      </div>

      <div class="form-actions">
        <button type="button" @click="handleCancel">Cancel</button>
        <button type="submit">Save</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      comicBook: {
        sku: '',
        name: '',
        genre: '',
        isbn: '',
        weight: '',
        price: '',
        releaseDate: '',
        description: '',
        authors: [],
        publisher: {
          name: '',
          yearFound: '',
        },
        photo: null,
      },
      authorsList: []
    };
  },
  mounted() {
    const bookId = this.$route.params.id; // Get the book ID from the route
    console.log('Received Book ID:', bookId);
    this.fetchComicBook(bookId);
  },
  methods: {
    fetchComicBook(bookId) {
      console.log('Read id: ', bookId);
      let bookIdStr = Number(bookId);
      fetch(`/comic_book/read/${bookIdStr}`)
          .then(response => response.json())
          .then(data => {
            console.log('Fetched data:', data);
            this.comicBook = data; // Populate form with the fetched data
          })
          .catch(error => {
            console.error('Error fetching comic book data:', error);
          });
    },
    handleFileUpload(event) {
      this.form.photo = event.target.files[0];
    },
    handleCancel() {
      // Handle cancel action
      console.log('Form canceled');
    },
    handleSubmit() {
      // Handle form submission
      console.log('Form data:', this.form);
    },
  },
};
</script>

<style scoped>
.form-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-group textarea {
  height: 100px;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.form-actions button {
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form-actions button[type="button"] {
  background-color: #f5f5f5;
  color: #333;
}

.form-actions button[type="submit"] {
  background-color: #4caf50;
  color: white;
}

.form-actions button[type="reset"] {
  background-color: #dc3545;
  color: white;
}

</style>
