<template>
  <div class="form-container">
    <form @submit.prevent="handleSubmit">
      <div class="form-grid">
        <div class="form-group">
          <label>SKU</label>
          <input v-model="form.sku" type="text" placeholder="Enter SKU" />
        </div>
        <div class="form-group">
          <label>Name</label>
          <input v-model="form.name" type="text" placeholder="Enter name" />
        </div>
        <div class="form-group">
          <label>Genre</label>
          <input v-model="form.genre" type="text" placeholder="Enter genre" />
        </div>
        <div class="form-group">
          <label>ISBN</label>
          <input v-model="form.isbn" type="text" placeholder="Enter ISBN" />
        </div>
        <div class="form-group">
          <label>Weight</label>
          <input v-model="form.weight" type="text" placeholder="Enter weight" />
        </div>
        <div class="form-group">
          <label>Price</label>
          <input v-model="form.price" type="text" placeholder="Enter price" />
        </div>
        <div class="form-group">
          <label>Release Date</label>
          <input v-model="form.releaseDate" type="date" placeholder="Enter release date" />
        </div>
        <div class="form-group">
          <label>Description</label>
          <textarea v-model="form.description" placeholder="Enter description"></textarea>
        </div>
      </div>

      <h3>Author Details</h3>
      <div class="form-grid">
        <div class="form-group">
          <label>First Name</label>
          <input v-model="form.author.firstName" type="text" placeholder="Enter first name" />
        </div>
        <div class="form-group">
          <label>Middle Name</label>
          <input v-model="form.author.middleName" type="text" placeholder="Enter middle name" />
        </div>
        <div class="form-group">
          <label>Surname</label>
          <input v-model="form.author.surname" type="text" placeholder="Enter surname" />
        </div>
      </div>

      <h3>Publisher Details</h3>
      <div class="form-grid">
        <div class="form-group">
          <label>Name</label>
          <input v-model="form.publisher.name" type="text" placeholder="Enter name" />
        </div>
        <div class="form-group">
          <label>Year Found</label>
          <input v-model="form.publisher.yearFound" type="text" placeholder="Enter year found" />
        </div>
      </div>

      <div class="form-group">
        <label>Upload Photo</label>
        <input type="file" @change="handleFileUpload" />
      </div>

      <div class="form-actions">
        <button type="button" @click="handleCancel">Cancel</button>
        <button type="submit">Save</button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        sku: '',
        name: '',
        genre: '',
        isbn: '',
        weight: '',
        price: '',
        releaseDate: '',
        description: '',
        author: {
          firstName: '',
          middleName: '',
          surname: '',
        },
        publisher: {
          name: '',
          yearFound: '',
        },
        photo: null,
      },
    };
  },
  methods: {
    handleFileUpload(event) {
      this.form.photo = event.target.files[0];
    },
    handleCancel() {
      // Handle cancel action
      console.log('Form canceled');
    },
    handleSubmit() {
      // Handle form submission
      console.log('Form data:', this.form);
    },
  },
};
</script>

<style scoped>
.form-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-group textarea {
  height: 100px;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.form-actions button {
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form-actions button[type="button"] {
  background-color: #f5f5f5;
  color: #333;
}

.form-actions button[type="submit"] {
  background-color: #4caf50;
  color: white;
}
</style>
